

























	export default {
		data() {
			return {
				showPassword: true,
				shows: 0,
				vertyText: "获取验证码",
				currentTime: 60,
				disabled: false,
				mobile:'',
				vertrycode:'',
				pass:''
			}
		},
		methods: {
			update:function(){
				if(!this.$utils.checkPass(this.pass)){
					uni.showToast({
						title: '请填写6位以上密码',
						mask: false,
						duration: 1500,
						icon:'none'
					});
				}
				this.$utils.tRequest({
					url: this.$webUrl+'user_getpassword',
					method: 'POST',
					data: {
						code:this.vertrycode ,
						mobile:this.user.mobile,
						password:this.pass
					},
					success: res => {
						uni.showModal({
							title: '提示',
							content: res.msg,
							showCancel: false,
							cancelText: '',
							confirmText: '知道了',
							success: res => {},
							fail: () => {},
							complete: () => {}
						});
					},
					fail: () => {},
					complete: () => {}
				})
			},
			getCode: function() {
				var that = this;
				console.log("this.$webUrl==" + this.$webUrl);
				if (!this.$utils.checkMobile(this.user.mobile)) {
					uni.showToast({
						title: '请输入正确手机号',
						mask: false,
						duration: 1500,
						icon:'none'
					});
					return;
				}
				this.$utils.tRequest({
					url: this.$webUrl + "user_verify_smscode",
					data: {
						mobile: this.user.mobile
					},
					success: res => {
						that.AppSendVerifyCode(res);
					},
					fail: () => {
				
					},
					complete: () => {
				
					}
				})
			},
			AppSendVerifyCode(res) {
				var that = this;
				if (res.code == 0) {
					var currentTime = that.currentTime;
					this.interval = setInterval(function() {
						this.disabled = true
						currentTime--;
			
						that.vertyText = currentTime + '秒'
						if (currentTime <= 0) {
							clearInterval(this.interval)
							that.vertyText = '重新发送',
								that.currentTime = 60,
								that.disabled = false
						}
			
					}, 1000)
				} else {
					uni.showModal({
						title: '提示',
						content: res.msg,
						showCancel: false,
						cancelText: '',
						confirmText: '知道了',
						success: res => {},
						fail: () => {},
						complete: () => {}
					});
				}
			},
			changePassword: function() {
				this.showPassword = !this.showPassword;
			},
			phoneNum: function() {
				this.shows = 1;
			},
			code: function () {
				this.shows = 2;
			},
			newPwd: function() {
				this.shows = 3;
			},
			resetBorder: function() {
				this.shows = 0;
			}
		},
		computed: {
          user(){
			  return this.$store.getters.getUserInfo;
		  },
		  phone(){
			  return this.$utils.defaultPlusXing(this.user.mobile)
		  }
		}

	}
